@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans-regular.woff2") format("woff2"), url("../fonts/opensans-regular.woff") format("woff");
  font-display: swap;
  unicode-range: U+0410-044f, U+0000-007f;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/opensans-bold.woff") format("woff");
  font-display: swap;
  unicode-range: U+0410-044f, U+0000-007f;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
  font-display: swap;
  unicode-range: U+0410-044f, U+0000-007f;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.container {
  width: calc(100% - 60px);
  margin: 0 auto;
}
@media (min-width: 640px) {
  .container {
    width: calc(100% - 100px);
  }
}
.about {
  padding: min(3.1rem, 46px) 0 min(1.6rem, 24px);
  background-color: #ffffff;
}
@media (min-width: 640px) {
  .about {
    padding: min(5.6rem, 71px) 0 min(1.8rem, 23px);
  }
}
@media (min-width: 1024px) {
  .about {
    padding: min(6.8rem, 113px) 0 min(2.6rem, 43px);
