  .contacts {
    padding: min(6.9rem, 88px) 0 min(5.7rem, 73px);
  }
}
@media (min-width: 1024px) {
  .contacts {
    display: grid;
    grid-template-columns: min(52.3rem, 873px) min(52.7rem, 879px);
    grid-template-rows: repeat(3, auto);
    column-gap: min(5.1rem, 85px);
    padding: min(7rem, 117px) 0 min(10.5rem, 175px);
  }
}
@media (min-width: 640px) {
  .contacts__wrapper {
    position: relative;
