  .contacts__wrapper::before {
    content: "";
    position: absolute;
    top: max(-2.3rem, -29px);
    right: min(0.3rem, 4px);
    width: min(4.7rem, 60px);
    height: min(8.1rem, 103px);
    background-image: url("../img/sprite.svg#flag");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}
@media (min-width: 1024px) {
  .contacts__wrapper {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    padding-left: min(17.4rem, 290px);
  }
  .contacts__wrapper::before {
    top: max(-1.7rem, -28px);
    right: 0;
    left: min(6.3rem, 105px);
    width: min(4.7rem, 78px);
    height: min(8.1rem, 135px);
