.decor--primary {
  position: relative;
}
.decor--primary::after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 0.6rem;
  background-image: url("../img/sprite.svg#zigzag");
  background-repeat: no-repeat;
}
@media (min-width: 640px) {
  .decor--primary::after {
    height: max(1.7rem, 17px);
    background-image: url("../img/sprite.svg#zigzag-tablet");
  }
}
@media (min-width: 1024px) {
  .decor--primary::after {
    width: calc(100% - 100px);
    height: clamp(23px, 2.3rem, 38px);
    left: 50%;
    transform: translateX(-50%);
    background-image: url("../img/sprite.svg#zigzag-desktop");
  }
}
.decor--secondary {
  position: relative;
}
@media (min-width: 640px) {
  .decor--secondary::after {
    content: "";
    position: absolute;
    bottom: -2px;
    width: 100%;
    height: max(2.4rem, 24px);
    background-image: url("../img/sprite.svg#wave-tablet");
    background-repeat: no-repeat;
  }
}
@media (min-width: 1024px) {
  .decor--secondary::after {
    height: clamp(34px, 3.4rem, 57px);
    background-image: url("../img/sprite.svg#wave-desktop");
  }
}
