.navigation__toggle::after {
  position: absolute;
  width: 2rem;
  height: 0.2rem;
  background-color: #231f20;
  transition: 0.3s;
}
.navigation__toggle {
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.navigation__toggle::before {
  content: "";
  top: max(-0.5rem, -8px);
  left: 0;
  transition: top 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
.navigation__toggle::after {
  content: "";
  right: 0;
  bottom: max(-0.5rem, -8px);
  transition: bottom 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
.navigation__toggle--open {
  background-color: transparent;
}
.navigation__toggle--open::before {
  content: "";
  top: 0;
  transform: rotate(45deg);
  transition: top 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
}
.navigation__toggle--open::after {
  content: "";
  bottom: 0;
  width: 100%;
  transform: rotate(-45deg);
  transition: bottom 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
}
